.example {
  background-color: #ffc107;
}

.sectionHeadlineMain .sectionHeadlineTitle {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 10px;
  font-size: 46px;
  margin-top: 60px;
}

.sectionHeadlineMain .sectionHeadlineInnerText {
  text-align: center;
  text-transform: uppercase;
  margin-top: 0;
  padding-top: 0;
  margin-bottom: 60px;
  font-size: 20px;
}

.threeGallery {
  width: 100%;
  display: flex;
}

.boxStylez {
  width: 333px;
  border: 5px solid white;
  margin: 0 auto;
  height: 330px;
  overflow: hidden;
}

.boxStylez .bsPrice {
  text-align: right;
  margin-top: 0;
  color: orange;
}

.boxStylez .bsText {
  margin-bottom: 0;
  font-weight: 100;
  color: #300804;
}

.boxStylez .bsInnerInner {
  height: 250px;
  overflow: hidden;
}

.boxStylez .bsInnerInner .boxStylezInner {
  width: 100%;
  height: 250px;
  background-repeat: no-repeat;
  background-size: cover;
  -moz-transition: all 0.3s;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.boxStylez .bsInnerInner .boxStylezInner:hover {
  -moz-transform: scale(1.1);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.featured {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 130px;
  padding-bottom: 100px;
}

.featured .featuredLeftImage {
  max-width: 500px;
  width: 500px;
  height: 500px;
  background-size: cover;
  float: left;
  margin-right: 82px;
}

.featured .featuredLeftText {
  margin-top: 0px;
}

.featured .featuredLeftText h2 {
  margin-top: 0px;
  color: #363639;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 48px;
}

.featured .featuredLeftText p {
  font-weight: 500;
  font-size: 19px;
  color: #363639;
}

.featured .featuredLeftText .buttonP {
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 29px;
  padding: 0;
  font-size: 18px;
  line-height: 26px;
  margin-top: 50px;
}

.featured .featuredLeftText .buttonP a {
  background: #363639;
  border: 1px solid #363639;
  border-radius: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  color: #fff;
  font-family: 'Work Sans', sans-serif;
  font-size: 14px;
  font-weight: 900;
  letter-spacing: 0.1em;
  line-height: 58px;
  margin-top: 30px;
  padding: 25px 47px;
  text-transform: uppercase;
  transition: .3s;
}

.featured .featuredLeftText .buttonP a:hover {
  background: transparent;
  border: 1px solid #363639;
  border-radius: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  color: black;
  font-family: 'Work Sans', sans-serif;
  font-size: 14px;
  font-weight: 900;
  letter-spacing: 0.1em;
  line-height: 58px;
  margin-top: 30px;
  padding: 25px 47px;
  text-transform: uppercase;
}

@-webkit-keyframes floating-arrow {
  from {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }

  65% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
  }

  to {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
}

@keyframes floating-arrow {
  from {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }

  65% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
  }

  to {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
}

@-webkit-keyframes floating-arrow-up {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  25% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
  }

  75% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
}

@keyframes floating-arrow-up {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  25% {
    -webkit-transform: translateY(4px);
    transform: translateY(4px);
  }

  75% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
  }
}

.mbrArrowFloating {
  bottom: 48px;
  left: 0;
  position: absolute;
  text-align: center;
  width: 100%;
}

.mbrArrowFloating .mbrArrowIcon {
  -webkit-animation: floating-arrow 1.6s infinite ease-in-out 0s;
  animation: floating-arrow 1.6s infinite ease-in-out 0s;
  background: rgba(255,255,255,0.5);
  border-radius: 3px;
  color: #4d4d4d;
  display: inline-block;
  height: 46px;
  outline-style: none !important;
  position: relative;
  text-decoration: none;
  transition: all .3s ease-in-out;
  width: 46px;
}

.mbrArrowFloating .mbrArrowIcon::before {
  content: "\203a";
  display: inline-block;
  font-family: serif;
  font-size: 32px;
  line-height: 1;
  font-style: normal;
  left: 4px;
  position: relative;
  top: 6px;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

